<template>
  <div class="logout">
    <el-col :span="12">
      <el-dropdown @command="logoutHandler">
        <span class="el-dropdown-link">
          {{userInfo.username}}
          <i class="el-icon-caret-bottom"></i>
        </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item @click="logoutHandler">退出登录</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </el-col>
  </div>
</template>

<script>
export default {
  computed: {
    userInfo() {
      return this.$store.state.userInfo;
    }
  },
  methods: {
    logoutHandler() {
      this.$store.commit({
        type: "clear_user_info"
      });
      this.$router.replace({ path: "/login" });
    }
  }
};
</script>

<style scoped lang="scss">
.logout {
  // width: 2rem;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  .el-dropdown-link {
    width: 1rem;
    display: flex;
    align-items: center;
    cursor: pointer;
    color: $active;
    .el-icon-caret-bottom {
      color: $text;
    }
  }
}
</style>


